<template>
  <div class="fixed top-1/4 left-2/4 -translate-x-2/4 -translate-y-2/4 -z-10 opacity-40 w-[500px] md:w-[1920px] darkMode">
    <img src="@/assets/images/cal_bg.jpg" alt="bg_img">
  </div>
  <div>
    <div class="w-full px-[2%]">
      <div class="max-w-7xl mx-auto">
        <div class="mt-10 flex pt-8 flex-wrap justify-between">
          <p class="basis-1/12 darkMode">CACULATOR</p>
          <ul class="flex justify-between basis-1/12">
            <li class="basis-2/4 text-center cursor-pointer">
              <font-awesome-icon :icon="isDark === true ? 'sun' : 'moon'" class="text-2xl darkMode" @click="toggleDark()"  />
            </li>
            <li class="basis-2/4 text-center cursor-pointer">
              <font-awesome-icon icon='globe' class="text-2xl darkMode" />
            </li>
          </ul>
        </div>
        <div class="flex mt-8 flex-wrap justify-between">
          <div class="p-10 mb-5 basis-full md:basis-[49%] darkMode">
            <h3 class="mb-2 darkMode">퍼센트 비율 값 계산</h3>
            <!-- change event는 input이나 select 태그와 많이 쓰임. --> <!-- v-model에서 앞에 바인딩을 하여 :v-model을 하면 안된다. v-model="calc1"는 안보이지만 input에 입력한 값을 밑에서 calc1이라고 하였으므로 값을 입력하면 calc1 값이 이 저장된다. 그래서 안보이지만 값이 저장된다고 하여 가상 모델 즉 v-model 이라고 하는것이다. -->
            <input @input="bindNumber" @change="UpdateCalc" v-model="calc1" type="text" class="border rounded p-2 text-right mb-2 text-black" placeholder="전체값 100"> <span class="mr-2 darkMode">의</span>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" placeholder="비율 값 20%" v-model="calc2"> <span class="mr-2 darkMode">%의 값은</span>
            <input type="text" class="border rounded p-2 text-right text-black" placeholder="일부 값 20" :value="ResultCalcA" readonly> <span class="darkMode">입니다.</span> <!--readonly는 value에 값을 적으면 고쳐지지 않고 고정된다.-->
          </div>
          <div class="p-10 mb-5 basis-full md:basis-[49%] darkMode">
            <h3 class="mb-2 darkMode">일정 값 비율 계산</h3>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" v-model="calc3"> <span class="mr-2 darkMode">의</span>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" v-model="calc4"> <span class="mr-2 darkMode">의 값은</span>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" :value="ResultCalcB" readonly> <span class="darkMode">% 입니다.</span>
            <span class="text-xs ml-4 darkMode">소수점 자리</span>
            <select v-model="sosu" class="ml-2 h-6">
              <option v-for="i in 5" :key="i" :value="i" class="text-xs">{{ i }}</option>
              <!-- value 값 앞에는 바인딩해서 value가 아닌 :value가 와야한다. : 을 써줘야 value 값이 데이터로 날아간다. 따라서 앞에 : 은 항상 써줘야한다. -->
              <!-- 
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
              -->
            </select>
          </div>
          <div class="p-10 mb-5 basis-full md:basis-[49%] darkMode">
            <h3 class="mb-2 darkMode">기준 > 변경값 비율 계산</h3>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" v-model="calc5"> <span class="mr-2 darkMode">의</span>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" v-model="calc6"> <span class="mr-2 darkMode">의 값이 변경되면</span>
            <input type="text" class="border rounded p-2 text-right mb-2 text-black" :value="ResultCalcC" readonly> <span class="darkMode"> 입니다.</span>
          </div>
          <div class="p-10 mb-5 flex items-center flex-wrap basis-full md:basis-[49%] darkMode">
            <h3 class="basis-full mb-2 darkMode">기존 > 변경값 비율 계산</h3>
            <div>
              <input type="text" class="border rounded p-2 text-right mb-2 text-black" v-model="calc7"> <span class="mr-2 darkMode">값이</span>
              <input type="text" class="border rounded p-2 text-right mb-2 text-black" v-model="calc8"> <span class="mr-2 darkMode">% 변경 될 시</span>
            </div>
            <div class="flex flex-col ml-10">
              <div>
                <span class="mr-2 darkMode">증가값</span>
                <input type="text" class="border rounded p-2 text-right text-black" :value="ResultCalcD" readonly> <span class="darkMode"> 입니다.</span>
              </div>
              <div class="mt-5 darkMode">
                <span class="mr-2 darkMode">감소값</span>
                <input type="text" class="border rounded p-2 text-right text-black" :value="ResultCalcE" readonly> <span class="darkMode"> 입니다.</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {useDark, useToggle} from '@vueuse/core';
export default {
  name: 'App',
  data() {
    return {
      calc1: '',
      calc2: '',
      calc3: '',
      calc4: '',
      calc5: '',
      calc6: '',
      calc7: '',
      calc8: '',
      sosu: '1',
      isDark : useDark(),
      toggleDark : useToggle(useDark()),
    }
  },
  methods:{
    UpdateCalc(){
      console.log("1111")
    },
    bindNumber(e){
      this.calc1 = e.target.value;
    }
  },
  computed:{
    ResultCalcA(){
      return this.calc1 === '' || this.calc2 === '' ? '' : (Number(this.calc1) * Number((this.calc2/100))).toFixed(3)
      //위에 v-model을 쓰면 숫자를 써도 문자로 되기 때문에 항상 숫자를 쓸때는 Number(숫자) 이렇게 써줘야 한다.
      // toFixed()는 소수점 몇 번째 자리까지 나타낼 수 있는지 나타내는 것이다. toFixed(3)은 소수점 3째 자리까지 나타낸다.
    },
    ResultCalcB(){
      return this.calc3 === '' || this.calc4 === '' ? '' : ((100 * Number(this.calc4)) / Number(this.calc3)).toFixed(this.sosu)
    },
    ResultCalcC(){
      let a = (this.calc6 - this.calc5) / this.calc5 * 100; // 위에 data return에 변수는 return처럼 보낼때 쓰이는 변수만 써준다 즉 return에 있는 변수인 calc1이나 calc2는 써주지만 위에 a는 우리가 변수를 위에 전송하여 나타내는 것이 아니기 때문에 위에 data return에 a를 안써줘도 된다.
      return this.calc5 === '' || this.calc6 === '' ? '' : a > 0 ? a.toFixed(0)+"% 증가" : Math.abs(a).toFixed(0)+"% 감소"
    }, // Math.abs(Num)은 절대값을 반환한다. 따라서 만약 Num 값이 -20 이라면 Math.abs(Num) 값은 20이 된다.
    ResultCalcD(){
      return this.calc7 === '' || this.calc8 === '' ? '' : (Number(this.calc7) + Number(this.calc7) * Number(this.calc8 / 100)).toFixed(0)
    },
    ResultCalcE(){
      return this.calc7 === '' || this.calc8 === '' ? '' : (Number(this.calc7) - Number(this.calc7) * Number(this.calc8 / 100)).toFixed(0)
    },
  },
  watch:{
    calc1: function(){
      return this.calc1 = this.calc1.replace(/[^0-9]/g, '');
    },
    calc2: function(){
      return this.calc2 = this.calc2.replace(/[^0-9]/g, '');
    },
  }, // watch는 어떠한 것을 방지하는 것으로 여기서는 숫자를 와야하는 자리에 문자가 오는것을 막기 위해 감시하는 역할이다.
  components: {
  },
}
</script>

